/* eslint-disable */
import axios from 'axios'
import { Message } from 'element-ui'

const base = config.ip

axios.interceptors.request.use(
  (config) => {
    if (sessionStorage.getItem('token')) {
      config.headers.Authorization = sessionStorage.getItem('token')
    }
    return config
  },
  (err) => {
    Promise.reject(err)
  }
)

// http response 拦截器

axios.interceptors.response.use(
  (response) => {
    return response
  },
  (error) => {
    if (error.response) {
      if (error.response.status !== 200) {
        Message({
          showClose: true,
          message: '网络错误，请稍后再试',
          type: 'error'
        })
      }
    }
    return Promise.reject(error.response.data) // 返回接口返回的错误信息
  },
)

export const bbb = (params) => axios.get(`${base}/config/tOrg/getTOrgTree`, { params })
export const tOrgInsertA = (params) => { return axios.post(`${base}/config/tOrg/insert`, params).then(res => res.data) }
