import Vue from 'vue';
import VueRouter from 'vue-router';

// 处理跳转相同路由报错问题
const routerPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return routerPush.call(this, location).catch((error) => error)
}

const importView = (file) => () => import(`@/views/${file}`)

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'yuwen',
    component: importView('yuwen'),
  },
  {
    path: '/yuwen/suiji',
    name: 'suiji',
    component: importView('yuwen/suiji'),
  },
  {
    path: '/home',
    name: 'Home',
    redirect: 'list',
    component: importView('Home'),
    children: [
      {
      path: '/list',
      name: 'list',
      meta: { title: '选项1' },
      component: importView('list'),
    },
    {
      path: '/answer',
      name: 'answer',
      meta: { title: '选项1' },
      component: importView('answer'),
    },
    {
      path: '/content',
      name: 'content',
      meta: { title: '选项1' },
      component: importView('content'),
    },
  ],
  },
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
});

export default router;
